* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Helvetica Neue";
  src: url('/public/fonts/helvetica-neue/helvetica-neue-regular.ttf');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Helvetica Neue";
  src: url('/public/fonts/helvetica-neue/helvetica-neue-medium.ttf');
  font-weight: 500;
  font-style: normal;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #b9b9b9;
}

::-webkit-scrollbar-thumb:hover {
  background: #b4b4b4;
}
/* End Scrollbar */

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

img {
  max-width: 100%;
  max-height: 100%;
}

.underlined-textbox fieldset {
  border: none;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-radius: 0;
}

.underlined-textbox .MuiOutlinedInput-root {
  background: transparent !important;
}

.underlined-textbox .MuiInputBase-input {
  padding: 6px 0 7px !important;
  height: 1.1876em !important;
}

.outlined-textbox {
  border-style: solid !important;
  border-width: 1px !important;
  text-overflow: ellipsis;
  border-radius: 8px;
  height: 48px;
  background-color: #fff;
}

.outlined-textbox .MuiInputBase-root {
  height: 100%;
  width: 100%;
}

.outlined-textbox .MuiInputBase-root:before {
  content: none;
}
.outlined-textbox .MuiInputBase-root:after {
  content: none;
}

.outlined-textbox .MuiInputBase-input {
  box-sizing: border-box;
  padding: 12px;
  height: 100%;
}